import React from 'react'
import invisibleMan from '../assets/invisible-man.jpg'

export default function Filme() {
  return (
    <div className='Filme'>
      <img src={invisibleMan} alt='O Homem Invisível (2020)'/>
      <main>
        <h1>O HOMEM INVISÍVEL (2020)</h1>
        <p><b>O marido de Cecilia morreu?</b> Sim, mas só no final.</p>
        <p><b>Como ele ficou invisível?</b> Ele utiliza uma roupa de alta tecnologia.</p>
        <p><b>Quais pessoas morrem no filme?</b> Vários policiais, o marido e a irmã de Cecilia.</p>
      </main>
    </div>
  )
}